h2 {
    margin-bottom: 10px;
}

body{
    margin: 0px auto;
    display: flex;
    height: 100%;
    width: 75%;
    background-color: #333;
    color: #EEE;
    padding: 40px;
}

*, *::before, *::after {
    box-sizing: border-box;
}

.solid1 {
    width: 50%;
    margin-left: 0px;
}

.solid2 {
    width: 80%;
    margin-left: 0px;
}

.head {
    width: 70%;
    /* background-color: #6cf; */
    height: 10%;
    font-family: 'Open Sans', sans-serif;
}

.head .name {
    font-size: 2em;
    margin-bottom: -10px;
}

.head .job {
    font-size: 0.9em;
    margin-top: 0px;
    padding-left: 3px;
}

.skill {
    height: %;
    width: 70%;
    /* background-color: lightblue; */
}

.skill_list {
    height: 100%;
    /* background-color: lightcoral; */
    font-size: 0.8em;
}

.skill_box {
    height: 40%;
    width: 33.33%;
    padding-right: 20px;
    float: left;
    font-family: 'Open Sans', sans-serif;
    /* background-color: lightpink; */
}

.skill_name {
    font-weight: bold;
}

.arrow:after {
    position: relative;
    /* top: 3pt; Uncomment this to lower the icons as requested in comments*/
    content: "";
    display: inline-block;
    /* By using an em scale, the arrows will size with the font */
    width: 0.4em;
    height: 0.4em;
    border-right: 0.2em solid linen;
    border-top: 0.2em solid linen;
    transform: rotate(45deg);
    margin-bottom: 0.1em;
    margin-left: 0.3em;
    margin-right: 0.3em;
}

.skill:after {
    content: "";
    display: table;
    clear: both;
}

.education {
    height: 15%;
    width: 47%;
    /* background-color: lawngreen; */
}

.education div {
    font-family: 'Open Sans', sans-serif;
    font-size: 0.9em;
    line-height: 30%;
    margin-bottom: 40px;
}

.language {
    width: 50%;
    height: 15%;
    /* background-color: khaki; */
}

.language .language-box{
    font-family: 'Open Sans', sans-serif;
    font-size: .9em;
    display: flex;
    line-height: 0px;
    margin-top: -10px;
}

.lang {
    float: left;
    width: 30%;
    font-weight: bold;
}

.level {
    float: left;
    margin-left: 20px;
    width: 50%;
}

.language-box:after {
    content: "";
    display: table;
    clear: both;
}

.row2 {
    height: 20%;
    display: flex;
    /* background-color: linen; */
}

.row2 .language{
    height: 100%;
    float: left;
}

.row2 .education{
    height: 100%;
    float: left;
}

.row2:after {
    content: "";
    display: table;
    clear: both;
}

.profile {
    margin-top: 50px;
    font-family: 'Open Sans', sans-serif;
    height: 15%;
    line-height: 70%;
    /* background-color: lawngreen; */
}

.interests {
    font-family: 'Open Sans', sans-serif;
    height: 10%;
    line-height: 100%;
    /* background-color: lightblue; */
}

.mail-icon {
    margin-top: 4px;
    margin-right: 8px;
    float: left;
    background-color: mintcream;
    overflow: hidden;
}

.mail-icon:after {
    content: "";
    display: table;
    clear: both;
}

.github-icon {
    margin-top: 4px;
    margin-right: 8px;
    float: left;
    background-color: mintcream;
    overflow: hidden;
    border-radius: 100%;
}

.github-icon:after {
    content: "";
    display: table;
    clear: both;
}
.item {
    width: 180px;
    margin-bottom: 8px;
    padding: 8px;
    border-bottom: 3px solid;
    border-radius: 5px;
    background-color: #fff;
}

.item .img-hover-zoom{
    overflow: hidden;
}

.item .img-hover-zoom:hover{
    background-color: rgba(0, 0, 0, 0.7);
}

.item .image {
    display: block;
    width: 100%;
    height: 109px;
}

.img-hover-zoom:hover:after {
    opacity: 0.7;
}

.item img {
    transition: transform .5s ease;
    background-color: rgba(0, 0, 0, 0.7);
}

.item .image:hover{
    transform: scale(1.5);
}

.item .category{
    margin: 15px 9px 10px;
    color: #aaa;
    letter-spacing: 1px;
    font-family: "Trebuchet MS", "Hiragino Kaku Gothic ProN", Meiryo, sans-serif;
}

.item .category::before{
    content: '';
    display: inline-block;
    width: 19px;
    height: 19px;
    margin-right: 5px;
    border: 2px solid;
    border-radius: 50%;
    vertical-align: -5px;
}

.item .description {
    margin: 10px;
    line-height: 1.5;
}

.item > a {
    display: block;
    margin: -8px -8px -11px;
    padding: 8px 8px 11px;
    border-radius: inherit;
    color: #777;
    transition: all 0.3s;
}

.item > a:hover {
    box-shadow: 0 0 6px -1px rgba(0, 0, 0, 0.5);
    opacity: 0.8;
}

.item-appetizer,
.item-appetizer .category::before {
    border-color: #FFC0CB;
}

.item-maindish,
.item-maindish .category::before {
    border-color: #76C047;
}

.item-column,
.item-column .category::before {
    border-color: #FFF100;
}

.item-breaktime,
.item-breaktime .category::before {
    border-color: #C1EFFF;
}

.item-M {
    width: 364px;
}

.item-M .image {
    height: 146px;;
}

.item-L {
    width: 732px;
}

.item-L .image{
    height: 403px;
}
.img-set {
    float: right;
    width: 85%;
}
.header {
    position: fixed;
    padding: 25px 0;
    border-bottom: none;
    text-align: center;
    width: 15%;
    height: 100%;
}

ul {
    list-style-type: none;
}

.logo {
    width: 136px;
    height: 136px;
    border-radius: 100%;
    transform: -webkit-transform 0.8s;
    transition: transform 0.8s;
}

.logo:hover{
    transform: rotate(360deg);
}

.nav{
    margin: 53px auto 10px;
}

.nav-item {
    margin-top: 20px;
    padding-right: 25px;
    text-align: center;
    letter-spacing: 1px;
    font-weight: bold;
    font-size: 1.3rem;
}

.nav-item a {
    display: inline-block;
}

.nav-item a::after {
    content: '';
    display: block;
    width: 0;
    margin: 6px auto 0;
    border-bottom: 1px solid #7C5119;
    transition: width 0.3s ease-in-out;
}

.nav-item a:hover::after {
    width: 100%;
}
